</template>

<script>
import data from "./assets/blog.js"

export default {
  name: 'App',
  components: {
    // List,
  },
  data(){
    return{
      data : data,
      idx  : 0
    }
  },
  watch : {
    $route(to){
      if(Object.keys(to.query).length == 0 && to.path.indexOf('list') > -1){
        this.$router.push('list')
        this.idx = 0;
      } else {
        this.idx = to.query.step;
      }
    }
  }

}
</script>

