<template>
  <div>
    <h5 @click="$router.push('/detail/'+step)">{{ data[step].title}}</h5>
    <p>{{ data[step].date }}</p>
    <button @click="stepUp">다음 프로젝트 목록 보기</button>
  </div>
</template>

<script>
export default {
    name:"List",
    props :{
        data : Object,
        step : Number,
    },
    methods : {
      stepUp() {
        if(this.step >1) {
          return false;
        } else {
          this.$emit('stepUp');
        }
      }
    },
    watch : {
      step() {
        if(this.step == 0){
          console.log('return')
        }else {
          this.$router.push('list?step='+this.step);
        }
      }
    },
}
</script>

